.SocialList{
    display: flex;
    list-style-type: none;
    padding-left: 0;
    margin-right: 30px;
}
.SocialList__container{
    display: flex;
    flex-direction: column-reverse;
}
.SocialList__element{
    width: 50px;
    height: 40px;
    margin-left: 60px;
    cursor: pointer;
}
.SocialList__text{
    color: #f2c067;
    font-size: 17px;
    padding-left: 10px;
}
.SocialList__link{
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.SocialList__link:hover{
    transition: linear .3s;
    opacity: .5;
}
.SocialList__home{
    width: 50px;
    height: 40px;
    cursor: pointer;
}
.SocialList__element:hover{
    transition: linear .3s;
    opacity: .5;
}
@media (max-width: 1200px) {
    .SocialList__element{
        margin-left: 20px;
    }
}
@media (max-width: 1050px) {
    .SocialList__element{
        margin-left: 18px;
        width: 45px;
        height: 35px;
    }
}
@media (max-width: 990px) {
    .SocialList__link{
        margin-top: 20px;
    }
}
