.BoxBrown{
    width: 395px;
    height: 395px;
}
@media (max-width: 1200px) {
    .BoxBrown{
        width: 300px;
        height: 300px;
    }
  }
  @media (max-width: 600px) {
    .BoxBrown{
        width: 250px;
        height: 250px;
    }
  }
  @media (max-width: 540px) {
    .BoxBrown{
        width: 200px;
        height: 200px;
    }
  }
  @media (max-width: 380px) {
    .BoxBrown {
        width: 180px;
        height: 180px;
    }
}