body{
  margin: 0;
}
.App {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #171232;
}
.App__main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.App__header{
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Silkscreen", Arial, Helvetica, sans-serif;

}
.App__titles{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App__content{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
}
.App__boxes{
  display: flex;
}
@media (max-width: 1050px) {
  .App__content{
     flex-direction: column;
  }
  .App__main{
    height: 100%;
  }
}
@media (max-width: 990px) {
  .App__header{
     flex-direction: column-reverse;
  }
}