.Promo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
}
.Promo__gif {
    position: static;
    display: center;
    width: 340px;
    height: 340px;
    border-radius: 20px;
}
@media (max-width: 1270px) {
    .Promo {
        width: 400px;
        height: 470px;
    }
}
@media (max-width: 440px) {
    .Promo {
        width: 300px;
        height: 370px;
    }
    .Promo__gif {
        width: 240px;
        height: 240px;
    }
}
@media (max-width: 380px) {
    .Promo {
        width: 280px;
        height: 350px;
    }
}
