.BoxRed {
    width: 395px;
    height: 395px;
}
@media (max-width: 1200px) {
    .BoxRed {
        width: 300px;
        height: 300px;
    }
}
@media (max-width: 600px) {
    .BoxRed {
        width: 250px;
        height: 250px;
    }
}
@media (max-width: 540px) {
    .BoxRed {
        width: 200px;
        height: 200px;
    }
}
@media (max-width: 440px) {
    .BoxRed {
        margin-left: -40px;
    }
}
@media (max-width: 380px) {
    .BoxRed {
        margin-left: -50px;
        width: 180px;
        height: 180px;
    }
}